html {
  background-color: #f3f5fc;
  padding: auto;
  margin: none;
  font-family: "Inter";
  font-style: normal;
}

.App {
  text-align: center;
  position: relative;
}

.App-logo {
  pointer-events: none;
  position: absolute;
  left: 8%;
  right: 37.43%;
  top: 2%;
  bottom: 0%;
  width: 48px;
  height: 48px;
  background-color: bisque;
}
.input-text {
  position: relative;
  display: flex;
  text-decoration: none;
  height: 40vh;
  justify-content: center;
  top: 15vh;
  font-weight: 400;
  font-size: 32px;
  line-height: 150%;
  color: #0a3871;
  background: none;
  initial-letter: initial;
  border: none;
  word-wrap: break-word !important;
}
.exclamation {
  height: 16px;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
}
.box-section {
  display: flex;
  flex-grow: 4;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.box-input {
  display: flex;
  position: relative;
  flex-direction: column;
  padding-left: 10px;
  padding-right: 10px;
  height: 100vh;
  width: 600px;
  max-width: calc(100% - 20px);
}
.box-main {
  display: flex;
  flex-direction: row;
}
.box-warning {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-top: 7px;
  top: 24vh;
  /* left: 200px; */
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #495057;
  opacity: 0.8;
  order: 1;
}
.box-buttons {
  display: flex;
  position: relative;
  justify-content: space-evenly;
  top: 35vh;
  min-width: 100%;
}
.primary-button {
  width: 298px;
  background: #0a3871;
  border-radius: 24px;
  border: none;
  height: 67px;
  color: #ffffff;
  margin-right: 20px;
}
.secondary-button {
  width: 298px;
  background: #d8dfe8;
  border-radius: 24px;
  border-color: #0a3871;
  border: 2px #0a3871 solid;

  height: 67px;
  color: #0a3871;
}

@media (max-width: 959px) {
  .box-main {
    flex-direction: column;
  }
  .box-input {
    height: 68vh;
    padding-right: none !important;
  }
  .input-text {
    height: 43vh;
  }
  .box-warning {
    top: 6vh;
  }
  .box-buttons {
    top: 17vh;
    flex-direction: row;
  }
  .primary-button {
    margin-right: 20px;
    margin-bottom: 0px;
  }
}
@media (max-width: 380px) {
  .box-buttons {
    top: 9vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .primary-button {
    margin-right: 0px;
    margin-bottom: 10px;
  }
  .input-text {
    top: 7vh;
  }
  .box-warning {
    top: -11vh;
  }
}
